@import "../../style/sass/mixin";
@import "../../style/sass/variables";

.menuHeader {
  background: $header-color;
  box-shadow: 0px 5px 10px rgba(91, 91, 91, 0.1);
}
.menu {
  ul {
    margin: 6px 0;
    li {
      list-style: none;
      display: inline-block;
      margin-right: 40px;
      &.active {
        a {
          &:after {
            -webkit-transform: scale(1);
            transform: scale(1);
          }
        }
      }
      &:hover {
        a {
          &:after {
            transform: scale(1);
            -webkit-transform: scale(1);
          }
          &:hover {
            text-decoration: none;
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }

      a {
        color: #fff;
        display: block;
        padding: 2px 0;
        position: relative;
        // &:last-child{
        //     &:after{
        //         display: inline;
        //     }
        // }
        &:after {
          position: absolute;
          background: red;
          content: "";
          left: 0;
          bottom: 0;
          width: 100%;
          height: 2px;
          -webkit-transform: scale(0);
          @include transition(all, 0.5s);
        }
      }
    }
  }
}
