@import "../../style/sass/mixin";

.product__item {
  margin-bottom: 35px;
  &:hover {
    .product__hover {
      li {
        opacity: 1;
        top: 0;
      }
    }
  }
}

.product__hover {
  position: absolute;
  text-align: center;
  bottom: 30px;
  left: 0;
  width: 100%;

  li {
    list-style: none;
    display: inline-block;
    position: relative;
    opacity: 1;
    margin-right: 10px;
    top: 100px;
    &:nth-child(1) {
      @include transition(all 0.4s ease 0.1s);
    }
    &:nth-child(2) {
      @include transition(all 0.4s ease 0.15s);
    }
    &:hover {
      a {
        background: red;
        span {
          color: #fff;
          -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
        }
      }
    }

    a {
      text-align: center;
      font-size: 18px;
      display: block;
      background: rgb(82, 80, 80);
      border-radius: 50%;
      height: 45px;
      width: 45px;
      line-height: 48px;
      color: #111111;
      @include transition(all 0.5s);

      span {
        position: relative;
        display: inline-block;

        -webkit-transform: rotate(0);
        transform: rotate(0);
        @include transition(all, 0.5s);
      }
    }
  }
}

.product__item__pic {
  position: relative;
  overflow: hidden;
  text-align: center;
}

.product__item__text {
  text-align: center;
  padding-top: 22px;
  h6 {
    a {
      font-size: 14px;
      color: #111111;
    }
  }

  .rating {
    line-height: 18px;
    margin-bottom: 5px;
    i {
      font-size: 10px;
      color: yellow;
      margin-right: 2px;
    }
  }
  .product__price {
    color: #111111;
    font-weight: 600;
    span {
      font-size: 14px;
      margin-left: 4px;
    }
  }
}
